.Logo{
    background-color: white;
    height: 80%;
    padding: 8px;
    position: relative;
    left: 6.5rem;
    box-sizing: border-box;   
    border-radius: 5px;
    margin: 0px auto 0px auto;
}

.Logo img{
    height: 100%;
}

@media(max-width: 499px){
    .Logo{
        height: 7%;
        position: fixed;
        right: 0.5rem;
        width: 64px;
        left: auto;
    }
}
