.SideDrawer{
    position: fixed;
    width: 17.5rem;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: #e8e8e8;
    padding: 2rem 1rem;
    max-width: 70%;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
}

@media (min-width: 500px) {
    .SideDrawer{
        display: none;
    }
}

.Open{
    transform: translateX(0);
}

.Close{
    transform: translateX(-100%);
}

.Logo{
    height: 2.8rem;
    width: 4rem;
}