.Burger{
    position: relative;
    width: 85%;
    height: 250px;
    margin: auto;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
    overflow: scroll;
    padding-top: 16px;
}

.Burger {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

.Burger::-webkit-scrollbar {
    display: none;
}

@media (min-width: 1000px) and (min-height: 700px){
    .Burger{
        width:  700px;
        height: 600px;
    }
}

@media (min-width: 500px) and (min-height: 401px){
    .Burger{
        width: 450px;
        height: 400px;  
    }
}

@media (min-width: 500px) and (max-height: 400px){
    .Burger{
        width: 350px;
        height: 300px;
    }
}