.Label{
    display: block;
    margin-bottom: 10px;
    font-weight: bold;
}

.Input{
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    margin-left: 9.4px;
}

.InputElement{
    outline: none;
    display: block;
    font: inherit;
    padding: 6px 10px;
    background-color: white;
    border: 1px solid #ccc;
    width: 100%;
    box-sizing: border-box;
    border-radius: 3.5px;
    
}

.InputElement:focus{
    outline: none;
    background-color: rgb(228, 227, 227);
}

.Invalid{
    border: 1.5px solid red;
    background-color: rgb(250, 131, 118);
}