.NavigationItem{
    margin: 16px 0px;
    box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItem a{
    color: #8F5C2C;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    color: #40A4C8;
}

@media (min-width: 500px){
    .NavigationItem{
        margin: 0px;
        display: flex;
        width: auto;
        align-items: center;
        height: 100%;
    }
    
    .NavigationItem a{
        color: white;
        padding: 16px 10px;
        height: 100%;
        border-bottom: 4px solid transparent;
    }
    
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active{
        background-color: #8F5C2C;
        border-bottom: 4px solid #40A4C8;
        color: white;
    }
}