.Toolbar{
    width: 100%;
    height: 3.5rem;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 20px;
    box-sizing: border-box;;
    background-color: #703B09;
}

.Toolbar nav{
    height: 100%;
}

@media(max-width: 499px){
    .DesktopOnly{
        display: none;
    }
}